.cards__list {
  display: flex;
  max-width: 280px;
  width: 100%;
  justify-content: space-between;
}

.cards__list--item {
  border: 2px solid transparent;
  width: 35px;
  height: 25px;
  border-radius: 3px;
}

.cards__list--item img {
  width: 100%;
  height: 100%;
  z-index: -10;
}

.cards__list--item:hover {
  border: 2px solid #8a5137;
}
